@import 'fonts'; // Шрифты
@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки
@import 'vars'; // Переменные
@import 'mixins'; // Миксины


// Компоненты 
@import 'components/footer';
@import 'components/header';
  



// Стили
*{
    transition: 0.2s linear;
}
.btn-secondary{
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #FFF61A;
    display: inline-block;
    padding: 24px 50px;
    border: 1px solid #FFFFFF;
    border-radius: 100px;
    &:hover{
        background: #fff;
        color: #000;
    }
}

.btn-primary--fill{
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: #000;
    display: inline-block;
    padding: 30px 92px;
    border: 1px solid #FFF61A;
    background: #FFF61A;
    
    box-shadow: 0px 4px 22px rgba(255, 246, 26, 0.6);
    border-radius: 60px;
    &:hover{
        background: #FAF118;
    }
}



.header {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 99;
    &__inner{
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #fff;
        padding: 30px 0px;
        a{
            color: #fff
        }
        div{
            display: flex;
            align-items: center;
            .phone-wrapper{
                flex-direction: column;
                align-items: flex-end;
                margin-right: 67px;
                .phone{
                    font-weight: 700;
                    font-size: 26px;
                    line-height: 100%;
                    margin-bottom: 10px;
                }
            }
        }

        .logo + a{
            font-weight: 600;
            font-size: 20px;
            line-height: 100%;
        }

        .logo{
            margin-right: 77px;
        }
    }
}

.home-page.main-screen{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    .main-screen__inner{
        min-height: 800px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        h1{
            font-weight: 600;
            font-size: 120px;
            line-height: 100%;
            color: #FFFFFF;
            margin-bottom: 60px;
        }
        .info{
            margin-bottom: 80px;
            ul{
                display: flex;

            }
            li{
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                display: flex;
                align-items: center;
                color: #FFFFFF;
                padding-left: 40px;
                &:not(:last-child){
                    margin-right: 100px;
                }
                position: relative;
                &::after{
                    content: '';
                    position: absolute;
                    width: 14px;
                    height: 14px;
                    background: #fff;
                    border-radius: 100%;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }

        .btn-primary--fill{
            max-width: 300px;
            margin-bottom: -35px;
        }
    }
   
}



.col-4{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.col-3{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.section{
    margin-top: 160px;
}

.title-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 80px;
    h2.title{
        font-weight: 600;
        font-size: 62px;
        line-height: 100%;
        color: #000000;
    }

    
}

.title-with-descr{
    .title{
        max-width: 600px;
        
    }

    .descr{
        max-width: 440px;
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: #676767;
    }
}

.task{
    &__list{
        margin-bottom: 60px;
        gap: 40px;
        &-item{
            background: #F8F8F8;
            border-radius: 6px;
            padding: 30px;
            min-height: 200px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            img{
                max-width: 40px;
            }

            .title{
                font-weight: 700;
                font-size: 20px;
                line-height: 38px;
                color: #047CA7;
            }

            .subtitle{
                font-weight: 400;
                font-size: 14px;
                line-height: 120%;
                color: #AEAEAE;
            }

        }
    }

    
}

.tasks{
    &__images{
        display: flex;
        justify-content: space-between;
        img{
            &:first-child{
                width: 30%;
            }
            &:nth-child(2){
                width: 40%;
            }
            &:nth-child(3){
                width: 25%;
            }
            max-height: 217px;
            height: auto;
            object-fit: cover;
            border-radius: 6px;
        }
    }
}


.img-banner{
    width: 100%;
    height: auto;
    border-radius: 6px;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        max-height: 400px;
        border-radius: 6px;
    }
}



.gallery{
    position: relative;
    z-index: 5;
    &-slider{
        width: 80%;
        height: 100%;
        margin: 0 auto;
        position: relative;
        &-wrapper{
            display: flex;
            width: 100%;
            height: 100%;
            position: relative;
        }
        &-slide{
            flex-shrink: 0;
            img{
                width: 100%;
                height: 100%;
                max-height: 500px;
                border-radius: 6px;
                object-fit: cover;
            }
        }

        .nav-wrapper{
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 113%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            .next-slide, .prev-slide{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 90px;
                height: 90px;
                background: #fff;
                border-radius: 100%;
                position: relative;
                z-index: 90;
                cursor: pointer;
            }
        }
    }
}


.gallery+ .contact-form{
    margin-top: -55px;
}


.contact-form{
    position: relative;
    z-index: 10;
   
    &__inner{
        background: #F8F8F8;
        border-radius: 6px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 90px 120px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        .form-info{
            max-width: 300px;
            width: 100%;
            h3{
                font-weight: 700;
                font-size: 36px;
                line-height: 100%;
                color: #000000;
                margin-bottom: 20px;
            }

            p{
                font-weight: 400;
                font-size: 20px;
                line-height: 120%;
                color: #676767;
                max-width: 230px;
            }
        }

        .form-fields{
            display: flex;
            align-items: center;
            background: #fff;
            border-radius: 30px;
            padding: 5px;
            input{
                height: 50px;
                &:first-child{
                    border-right: 1px solid #F8F8F8;
                }
            }
            .input-white{
                font-weight: 400;
                font-size: 14px;
                line-height: 100%;
                display: flex;
                align-items: center;
                color: #AEAEAE;
                padding-left: 30px;
                &::placeholder{
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 100%;
                    display: flex;
                    align-items: center;
                    color: #AEAEAE;
                }
            }
            .btn-primary--fill{
                padding: 18px 31px;
                font-weight: 500;
                font-size: 14px;
                line-height: 100%;
                cursor: pointer;
            }
        }
    }
}

.contact-form.white-text{
    h3, p{
        color: #fff !important;
    }

    
}

.products{
    &__inner{
        .product{
            &__list{
                gap: 40px;
            }

            &__item{
                &-img{
                    width: 100%;
                    max-height: 300px;
                    img{
                        width: 100%;
                        max-height: 300px;
                        height: auto;
                        object-fit: cover;
                        border-radius: 6px;
                    }
                    margin-bottom: 30px;
                }

                &-name{
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 100%;
                    color: #000000;
                    margin-bottom: 10px;
                }

                &-options{
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 120%;
                    color: #676767;
                    margin-bottom: 20px;
                }

                &-info{
                    display: flex;
                    flex-wrap: wrap;
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 100%;
                    color: #676767;
                    margin-bottom: 40px;
                    span{
                        margin-right: 25px;
                        font-weight: 300;
                    }
                }

                &-nav{
                    display: flex;
                    align-items: center
                }

                &-btn{
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 100%;
                    color: #000000;
                    padding: 23px 28px;
                    border: 1px solid #FFF61A;
                    border-radius: 60px;
                    margin-right: 20px;
                    &:hover{
                        background: #FFF61A;
                        box-shadow: 0px 4px 22px rgba(255, 246, 26, 0.6);
                    }
                }

                &-price{
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 100%;
                    color: #047CA7;
                }
            }
        }

        .view-all{
            display: block;
            width: 100%;
            padding: 23px 0px;
            text-align: center;
            font-weight: 500;
            font-size: 14px;
            line-height: 100%;
            border: 1px solid #F8F8F8;
            border-radius: 38px;
            margin-top: 80px;
            color: #000;
            &:hover{
                border-color: #047CA7;
            }
        }
    }
}



.steps__list{
    gap: 65px;
    &-item{
        .number{
            font-weight: 600;
            font-size: 36px;
            line-height: 120%;
            color: #047CA7;
            margin-bottom: 40px;
        }
        .text{
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: #676767;
            max-width: 240px;
        }
    }
}

.footer{
    background: linear-gradient(180deg, #FFFFFF 0%, #F9F9F9 100%);
    padding-top: 150px;
    padding-bottom: 80px;
    &__inner{
        .footer__row{
            display: flex;
            justify-content: space-between;
            .social{
                display: flex;
                align-items: center;
                a:not(:last-child){
                    margin-right: 15px;
                }
            }
            &:last-child{
                margin-top: 80px;
            }
            .address{
                font-weight: 600;
                font-size: 48px;
                line-height: 120%;
                color: #000000;
                max-width: 440px;
            }
            .contact{
                display: flex;
                flex-direction: column;
            }
            .phone{
                font-weight: 600;
                font-size: 48px;
                line-height: 100%;
                color: #047CA7;
                margin-bottom: 25px;
            }

            .email{
                font-weight: 400;
                font-size: 20px;
                line-height: 120%;
                color: #676767;
            }

            .copyright{
                font-weight: 300;
                font-size: 14px;
                line-height: 17px;
                color: #676767;
                max-width: 460px;
            }



        }
    }
}

@import 'media';  // Должен быть в конце проекта